@import url(https://fonts.googleapis.com/css2?family=Chivo:wght@400;900&display=swap);
:root {
  --color-base: rgb(31, 31, 31);
  --color-base-light: rgb(40, 40, 40);
  --color-base-lighter: rgb(47, 47, 47);
  --color-base-dark: rgb(26, 26, 26);
  --color-base-darker: rgb(20, 20, 20);

  --color-accent: rgb(151, 215, 0);
  --color-warn: rgb(216, 14, 18);
  --color-warn-light: hsl(359, 75%, 60%);

  --color-text: rgba(255, 255, 255, 1);
  --color-text-light: rgba(255, 255, 255, 0.72);
  --color-text-lighter: rgba(255, 255, 255, 0.38);
  
  --color-shadow: rgba(0, 0, 0, 0.5);
  --color-shadow-light: rgba(0, 0, 0, 0.25);
  --color-highlight: rgba(255, 255, 255, 0.2);
  --color-highlight-accent: rgba(151, 215, 0, 0.25);

  --border-radius-sm: 4px;
  --border-radius-md: 6px;
  --border-radius-lg: 10px;
  --border-radius-round: 50%;

  --anim-duration-short: 150ms;
}
body {
  margin: 0;
  font-family: 'Chivo', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: var(--color-base);
  color: var(--color-text);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Global styles */
span.character.style_alternate {
  -webkit-font-feature-settings: "ss04", "ss03";
          font-feature-settings: "ss04", "ss03";
}

span.character.number_oldstyle {
  -webkit-font-feature-settings: "onum";
          font-feature-settings: "onum";
}

a[href] {
  color: var(--color-accent);
}
.App header {
  padding-left: 4.25em;
}

.App h1 {
  font-size: 2.25em;
  font-weight: 900;
  margin: 1.5rem 0;
}

.App h1 em {
  font-style: normal;
  color: var(--color-accent);
}

.App main,
.App footer {
  padding: 0 4.25rem;
  margin: 1.5rem 0;
}

.App main {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template-rows: auto 1fr;
  grid-column-gap: 2.25rem;
  -webkit-column-gap: 2.25rem;
          column-gap: 2.25rem;
  grid-row-gap: 1.5rem;
  row-gap: 1.5rem;
  align-items: start;
}

.rules,
.preview {
  grid-column: 1 / span 1;
}

.selector-table {
  grid-column: 2 / span 1;
  grid-row: 1 / span 2;
}

.App footer hr {
  border: none;
  border-top: 1px solid var(--color-highlight);
  margin-bottom: 1em;
}

.App footer div {
  margin-top: 0.5em;
}

@media screen and (max-width: 1049px) {
  .App main {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, auto);
  }

  .selector-table {
    grid-column: 1 / span 1;
    grid-row: 2 / span 1;
  }
}
.preview {
  text-align: center;
}

.preview .characters {
  font-family: 'Source Sans 3', sans-serif;
  -webkit-text-decoration: underline var(--color-text-lighter) 1px;
          text-decoration: underline var(--color-text-lighter) 1px;
}

.preview .characters,
.preview .all-passed {
  font-size: 3.375em;
}
ul.rules {
  margin-left: -4.25em;
  padding-left: 4em;
  line-height: 1.5;
}

ul.rule-group {
  margin-bottom: 0.5em;
}

li.rule {
  padding-left: 0.25em;
}

.rule.passed {
  color: var(--color-text);
  list-style-type: '\2705';
}

.rule.failed {
  color: var(--color-warn-light);
  list-style-type: '\274C';
}

.rule.passed .rule.failed {
  color: var(--color-text-lighter);
  text-decoration: line-through;
  list-style-type: '\2796';
}
table.selector-table {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: 'Source Sans 3', sans-serif;

  border-spacing: 0;
  border-collapse: collapse;
  border-radius: var(--border-radius-md);

  background-color: var(--color-base-dark);
}

table.selector-table tr:not(:first-child) {
  border-top: 1px solid var(--color-highlight);
}

table.selector-table col:nth-child(2n) {
  background-color: var(--color-base-darker);
}

table.selector-table td {
  padding: 0.5em 0.5em;
}

table.selector-table td div.selector-cell-content {
  width: 100%;

  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  grid-gap: 0.2em;
  gap: 0.2em;

  max-width: calc(1.5em * 3 + 0.2em * 2);
}

table.selector-table span.character {
  height: 1.5em;
  width: 1.5em;
  line-height: 1.5em;
  text-align: center;

  border-radius: var(--border-radius-round);

  transition:
    background-color var(--anim-duration-short) ease-out,
    color var(--anim-duration-short) ease-out;
}

table.selector-table span.character:hover {
  background-color: var(--color-highlight);
}

table.selector-table span.character.selected {
  background-color: var(--color-shadow);
  color: var(--color-accent);
}

table.selector-table span.character.selected:hover {
  background-color: var(--color-highlight-accent);
}

@media screen and (max-width: 1599px) {
  table.selector-table {
    font-size: 0.875em;
  }

  table.selector-table td {
    padding: 0.25em;
  }
}
